body{
  overflow: hidden;
}

.MainLoaderBG{
    z-index: 1000;
  background: #2f2f2f;
}

.MainLoaderWrapper{
  height: 100vh;
    position: center;
  margin: auto;
  width: 50%;
    z-index: 1000;

 display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation-iteration-count: infinite;
}

.loading-img{
    height: 33vh;

}

.animated.rotate {
  -webkit-animation-duration: 500ms;
  animation-duration: 500ms;
}

.animated.fade {
  -webkit-animation-duration: 500ms;
  animation-duration: 500ms;
}
