@font-face {font-family: "UAF bold"; src: url('../../fonts/UAFSans-Bold.ttf');}
@font-face {font-family: "UAF medium"; src: url('../../fonts/UAFSans-Medium.ttf');}
@font-face {font-family: "UAF regular"; src: url('../../fonts/UAFSans-Regular.ttf');}
@font-face {font-family: "UAF semibold"; src: url('../../fonts/UAFSans-SemiBold.ttf');}



.modalA {
    font-family: "UAF regular", Arial, sans-serif;
	text-decoration: none !important;
    font-size: 1.2;
	color: white;
}
.modalA, .modalLabel{
	-webkit-tap-highlight-color: transparent;
    transition: .4s ease;
}
.modalA:hover {
    color: #F39200;
    transition: .4s ease;
}
.modalH1 {
    font-family: "UAF semibold", Arial, sans-serif;
    font-size: 3em;
    text-align: left;
    margin: 0;
    color: white;
}
.modalH2 {
    font-family: "UAF semibold", Arial, sans-serif;
    font-size: 2em;
    text-align: left;
    color: rgba(255,255,255,1);
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
}
.modalH3 {
    font-family: "UAF semibold", Arial, sans-serif;
    font-size: 1em;
    text-align: left;
    color: white;
    margin-block-start: 1em;
    margin-block-end: 1em;
}
details > summary {
    list-style: none;
    cursor: pointer;
    transition: .3s ease;
}

/*body*/
.modal {
    display: none; 
    position: fixed; 
    background-image: url('../../imgs/popup.png');
	background-repeat: no-repeat;
    background-size: cover;
    z-index: 100; 
    left: 5%;
    top: 4%;
    width: 90%; 
    height: 83%; 
	transition: .3s ease;
}
.modalText {
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 100%);
    display: flex;
	flex-direction: row;
    flex-wrap: wrap;
	align-items: center;
	justify-content: space-around;
    overflow: auto;
}
.modalTextA {
    width: 33%;
}
.modalTextB {
    width: 40%;
    padding-left: 1em;
    padding-right: 1em;
    padding-top: 1em;
    padding-bottom: 1em;
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
    overflow: auto;
}
.modalText1 {
    width: 100%;
    color: white;
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
    overflow: auto;
}
.modalText2 {
    width: 100%;
    /*border: 1px solid rgba(255,28,25,1);*/
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
    overflow: auto;
}
.modalText2 > h2 {
    color: #F39200;
}
.modalJoinButton {
    width: 100%;
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
    overflow: auto;
}
.modalJoin {
    padding-top: 0.6em;
    padding-left: 1em;
    padding-bottom: 0.6em;
    padding-right: 1em;
    text-decoration: none !important;
    background-color: #4E4634;
    font-family: "UAF regular", Arial, sans-serif;
    color: white;
}
.modalLink {
    padding-top: 1em;
    width: 100%;
    color: white;
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
    overflow: auto;
    cursor: pointer;
    text-decoration: underline;
}
.modalLink > a {
    color: white;
    cursor: pointer;
}
.modalLogoImg {
    width: 30%;
    min-width: 12em;
    max-width: 12em;
    max-height: 15em;
}  
.modalClose {
    top: 10%;
    left: 90%;
    position: fixed;
    color: white;
    float: right;
    font-size: 4em;
    font-weight: bold;
} 
.modalClose:hover,
.modalClose:focus {
    color: #F39200;
    text-decoration: none;
    cursor: pointer;
}

