.scrollTop {
    position: fixed;
    width: 100%;
    bottom: 20px;
    align-items: center;
    height: 20px;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
}
.scrollTop:hover{
    opacity: 1;
}
.decor {
    position: relative;
    max-width: 400px;
    margin: 50px auto 0;
    border-radius: 30px;
}
.form-left-decoration,
.form-right-decoration {
    content: "";
    position: absolute;
    width: 50px;
    height: 20px;
    background: #31372A;
    border-radius: 20px;
}
.form-left-decoration {
    bottom: 60px;
    left: -30px;
}
.form-right-decoration {
    top: 60px;
    right: -30px;
}
.form-left-decoration:before,
.form-left-decoration:after,
.form-right-decoration:before,
.form-right-decoration:after {
    content: "";
    position: absolute;
    width: 50px;
    height: 20px;
    border-radius: 30px;
    background: #6F6D5B;
}
.form-left-decoration:before {top: -20px;}
.form-left-decoration:after {
    top: 20px;
    left: 10px;
}
.form-right-decoration:before {
    top: -20px;
    right: 0;
}
.form-right-decoration:after {
    top: 20px;
    right: 10px;
}
.circle {
    position: absolute;
    bottom: 80px;
    left: -55px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #363c2e;
}
.form-inner {
    border-radius: 5px;
    margin-left: 40px;
    margin-right: 40px;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 110px;
    border: #dfd7cc;
}
.form-inner input,
.form-inner textarea {
    display: block;
    width: 100%;
    padding: 0 20px;
    margin-bottom: 10px;
    background: #E9EFF6;
    line-height: 40px;
    border-width: 0;
    border-radius: 20px;
    box-sizing: initial;
    font-family: 'Roboto', sans-serif;
}
.form-inner input[type="submit"] {
    margin-top: 30px;
    background: #f69a73;
    border-bottom: 4px solid #d87d56;
    color: white;
    font-size: 14px;
}
.form-inner textarea {resize: none;}
.form-inner h3 {
    margin-top: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 24px;
    color: #707981;
}
body{
    font-family: "Inter", serif;     
    font-style: normal;
    overflow-x: hidden;
    overflow-y: auto;
    background-color:#ffffff;
    font-size: 16px;

}
@media only screen and (min-width: 1920px) {
   
    .EmblemaForces{
        height:50%;
        color: #ffffff;
        text-align: center;
        opacity: 1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: opacity 0.4s ease;
    }
    .ForcesText {
        background: rgba(0, 0, 0, 0.39) no-repeat center top;
        background-size: contain;
        color: #ffffff;
        position: absolute;
        top: 0;
        left: 0;
        font-size: 2em;
        opacity: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        text-align: center;
        transition: opacity 0.4s ease;
    }
    .ForcesText:hover{
        opacity: 1;
    }
    .ForcesText:hover +.Wrap{
        display: none;
    }
    .dropdown-title{
        font-size: 1em;
    }
    .CardHeader {
        grid-area: photo;
        width: 100%;
        height: 324px;
        position: relative;
        border-radius: 18px;
        overflow: hidden;
    }
    .NewNewsCardHeader {
        grid-area: photo;
        width: 100%;
        height: 260px;
        position: relative;
        border-radius: 18px;
        overflow: hidden;
    }
    .NewNewsSlider{
        position: relative;
        display: grid;
        z-index: 0;
