        grid-template:
                "l s r" minmax(min-content, max-content)
                 /1fr 800px 1fr;
    }
    .CardHeader:hover .NewsCardHeaderImgOverlay {
         opacity: 1;
     }

    .Look {
        color: #ffffff;
        font-size: 1.1em;
    }

    .CardDate {
        padding-top: 10px;
        grid-area: date;
        width: fit-content;
        justify-self: start;
        margin: 2px;
        font-size: 0.8em;
        color: #b8bdcc;

    }

    .CardAbout {
        grid-area: about;
        width: 100%;
        justify-self: start;
        align-self: flex-start;
        text-align: left;
        font-size: 1em;
        font-weight: normal;
        font-style: normal;
        margin: 4px;
    }



    .Forces {
        grid-area: e;
        margin-top: 20px;
        margin-bottom: 20px;

    }
    .ForcesContainerTitle{
        font-size: 2em;
        font-family: 'GothamProRegular';
        padding: 10px;
        color: #000000;
        border-bottom: 1px solid #d06;
    }

    .NewsSliderWrapper {
        grid-area: s;
    }

    .SideMenuWrapper {
        grid-area: m;

    }

    .PrimaryNewsWrapper {
        margin-top: 20px;
        grid-area: a;
        align-self: start;
    }
    .a {
        font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    }

    .div {
        font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    }

    .p {
        font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    }

    .ContainerSlider {
        margin-top: 60px;        grid-area: s;
    }

    .Slider {
        position: relative;
        display: grid;
        z-index: 0;
        height: 500px;
        padding-right: 20px;
        padding-bottom: 10px;

        grid-template: "l s  r" minmax(min-content, max-content)
                 /1fr 1800px 1fr;


    }

    .slick-slider .slick-track,
    .slick-slider .slick-list {
        height: 440px;
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
        -o-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    .NewsDescription {
        white-space: pre-wrap;
        padding: 5px;
        font-size: 1em;
        font-weight: bold;
        flex-grow: 2;
    }

    .NewsItemsContainer {
        display: grid;
        grid-template-columns: repeat(1, 1fr);

        width: 90%;
        margin-left: 5%;
    }

    .News {
        font-size: 1em;
        white-space: pre-wrap;
    }

    .News :hover {
        color: #bdbdbd;
    }

    .NewsDate {
        font-size:1em;
        float: left;
        color: black;
        margin-right: 5px;
        padding-right: 5px;
        cursor: default;
    }



    .ForcesContainer {
        display: flex;
        width: 100%;
        height: 300px;
        grid-template-rows: repeat(5, 1fr);

    }

    .TextMenuSide {
        align-content: center;
        width: 100%;
        height: auto;
        font-size:1em;
        color: #ffffff;
        background: #000000;
        white-space: pre-wrap;
        cursor: default;
    }

    .ImgMenuSide {
        position: relative;
        height: 300px;
        margin-top:30px;
    }
    .TextMenuTitle{
        font-size: 1em;
    }
    .TextDropDownMenu{
        font-size: 0.9em;
    }
    .SideMenuWrapper{
        grid-area: m;
    margin-top: 60px;
    }
}
