@font-face{
    font-family:"Inter";
    src:url(./fonts/Inter-Regular.ttf) format("ttf");
    font-weight:400;
    font-style:"normal";font-display:swap}
    @font-face{font-family:"Inter";src:url(./fonts/Inter-Medium.ttf) format("ttf");font-weight:500;font-style:"normal";font-display:swap}
    @font-face{font-family:"Inter";src:url(./fonts/Inter-SemiBold.ttf) format("ttf");font-weight:600;font-style:"normal";font-display:swap}
    @font-face{font-family:"Inter";src:url(./fonts/Inter-Bold.ttf) format("ttf");font-weight:700;font-style:"normal";font-display:swap}

html, body{
    padding: 0;
    margin: 0;
    height: 100%;
    overflow-y: auto;
    font-family: 'Inter',"Arial",sans-serif!important;
}

.header__top.page-scrolled{
    height:0
}
.header__top{
    -webkit-box-pack:justify;
    -ms-flex-pack:justify;
    justify-content:space-between;
    height:60px;
    border-bottom:2px solid #ececec
}